import _fetch from '@/utils/axios'

// 获取用户信息
function getUserInfo () {
  return _fetch({
    url: '/v1_0/user',
    method: 'get',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 编辑页用户信息
function editUserInfo (id) {
  return _fetch({
    url: '/v1_0/user/profile',
    method: 'get',
    headers: {
      contentType: 'application/json'
    },
    body: {
      target: id
    }
  })
}
// 修改用户的信息
function editUserInformation (data) {
  return _fetch({
    url: '/v1_0/user/profile',
    method: 'PATCH',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

export { getUserInfo, editUserInfo, editUserInformation }
