import Vue from 'vue'
import VueRouter from 'vue-router'
import { getToken } from '@/utils/token'
import store from '@/store/index'
import { Toast } from 'vant'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/login/index')
  },
  {
    path: '/index/',
    name: 'Main',
    component: () => import('../views/main/index'),
    meta: {
      needTab: true
    },
    childre: []
  },
  {
    path: '/search',
    name: 'search',
    component: () => import('../views/main/search/search')
  },
  {
    path: '/searchResult',
    name: 'searchResult',
    component: () => import('../views/main/search/searchResult')
  },
  {
    path: '/user',
    name: 'User',
    component: () => import('../views/user/index'),
    meta: {
      needTab: true,
      needInfo: true
    }
  },
  {
    path: '/nothing1',
    name: 'Nothing1',
    component: () => import('../views/nothing1/index'),
    meta: {
      needTab: true
    }
  },
  {
    path: '/nothing2',
    name: 'Nothing2',
    component: () => import('../views/nothing2/index'),
    meta: {
      needTab: true
    }
  },
  {
    path: '/information',
    name: 'Information',
    component: () => import('../views/user/information'),
    meta: {
      needInfo: true,
      needTab: false
    }
  },
  {
    path: '/details/:id',
    name: 'details',
    component: () => import('../views/main/details'),
    meta: {
      needInfo: true
    }
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 取得页面需不需要登录？
  if (to.meta.needInfo) {
    // 有没有token
    if (getToken()) {
      // 是不是已经有信息啦？
      if (store.state.userState) {
        // 有信息就返回
        next()
      } else {
        // 没信息就重新获取
        store.dispatch('setUserInfoAsync')
        next()
      }
    } else {
      // token都没有？先登录吧
      Toast.fail('未登录')
      router.push('/login')
    }
  }
  next()
})

export default router
