import axios from 'axios'
import jsonbig from 'json-bigint'

// 全部使用后删除下一行
// eslint-disable-next-line no-unused-vars
import { removeToken, getToken, setToken } from './token'

const _fetch = axios.create({
  baseURL: 'http://toutiao-app.itheima.net',
  transformResponse: [
    // 将 超过 js 处理范围数字进行转换
    function (data) {
      try {
        // 就是服务器返回的 json 格式的字符串
        return jsonbig.parse(data)
      } catch {
        return JSON.parse(data)
      }
    }
  ]
})

// 创建一个新的 axios 实例来更新 token：
//  问题：为什么不用上面那个实例：因为上面这个实例，会带一个 token ,而这个 token 过期
// const _fetchToken = axios.create({
//   baseURL: 'http://ttapi.research.itcast.cn/'
// })

_fetch.interceptors.request.use(
  config => {
    // console.log('做点什么')
    if (getToken()) config.headers.Authorization = 'Bearer ' + getToken()
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

_fetch.interceptors.response.use(
  response => {
    // console.log(response)
    return response.data
  },
  error => {
    return Promise.reject(error)
  }
)

export default _fetch
